.campaign-map {
	.campaign-map-inner {
		width: 620px;
		height: 620px;
		margin: 0 auto;

		.campaign-map-square-container {
			position: relative;
			width: 100%;
			height: 100%;
	
			.campaign-map-square {
				aspect-ratio: 1 / 1;
				position: absolute;
				border: 0 solid black;
			}
		}
	}
}
